import './FAQUnlogged.css'
import React from 'react'
import {Accordion, Button} from "react-bootstrap";
import {Link} from "react-router-dom";
import Image from "../logo.png";

const FAQUnlogged = () => {
    return(
        <>

            <div className="home_top">
                <div>
                    <Link to="/"><img src={Image} alt="E-Floresta Logo" className="home_logo" /></Link>
                </div>
            </div>

            <div className="bg-img_FAQ">
                <div className="body_FAQ">
                    <br/>
                    <h2 className="title_FAQ"><b>Perguntas frequentes</b></h2>
                    <p className="description_FAQ">Nesta seco pode encontrar algumas das perguntas feitas com mais frequncia e as suas respetivas
                        respostas. Esperemos que lhe sejam teis.
                    </p>

                    <Accordion className="accordion_FAQ">
                        <Accordion.Item eventKey="0">
                            <Accordion.Header>Que dados preciso de fornecer para criar uma conta? </Accordion.Header>
                            <Accordion.Body>
                                Para criar uma conta dever fornecer pelo menos o seu nome completo e um e-mail funcional.
                                Poder adicionalmente, acrescentar o seu telemvel/telefone e NIF, se assim o entender, no momento da
                                criao da conta ou mais tarde, editando o seu perfil.
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="1">
                            <Accordion.Header>O que  necessrio para registar uma parcela?</Accordion.Header>
                            <Accordion.Body>
                                Para registar uma parcela dever fornecer as seguintes informaes: a localizao da parcela (inclui distrito,
                                concelho, freguesia, seco e nmero de artigo), as coordenadas dos seus limites (que podem ser marcadas num
                                mapa prprio, ou inseridas manualmente), o tipo de cobertura do solo, escolher uma utilizao atual e
                                prvia do solo dentre as opes disponveis no site, uma foto identificativa da parcela e um PDF que funcione
                                como comprovativo de que se trata do dono da mesma. Todos os campos descritos prviamente so obrigatrios.
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="2">
                            <Accordion.Header>Quem ir gerir a minha parcela?</Accordion.Header>
                            <Accordion.Body>
                                Cada parcela registada poder ser gerida por quem a registou/dono ou, caso o deseje, poder ser gerida
                                por uma entidade competente disponvel na sua freguesia ou concelho, atravs de um pedido que ter de ser
                                aceite pela mesma. Parcelas diferentes podem utilizar mtodos de gerenciamento diferentes ou at ser geridas
                                por entidades distintas.
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>

                    <div className="button_FAQUnlogged">
                        <Link to="/">
                            <Button id="faqButton" type="button">Voltar  pgina inicial</Button>
                        </Link>
                    </div>

                </div>
            </div>

        </>
    )
}

export default FAQUnlogged