import './FAQ.css'
import TopBar from '../TopBar/TopBar.js'
import CheckIfLoggedOut from "../util/CheckIfLoggedOut";
import React from 'react'
import {Accordion} from "react-bootstrap";
import CheckIfActive from "../util/CheckIfActive";

const FAQ = () => {
    return(
        <>
            <CheckIfLoggedOut />
            <CheckIfActive />
            <TopBar />

            <div className="bg-img_FAQ">
                <div className="body_FAQ">
                    <br/>
                    <h2 className="title_FAQ"><b>Perguntas frequentes</b></h2>
                    <p className="description_FAQ">Nesta seco pode encontrar algumas das perguntas feitas com mais frequncia e as suas respetivas
                        respostas. Esperemos que lhe sejam teis.
                    </p>

                    <Accordion className="accordion_FAQ">
                        <Accordion.Item eventKey="0">
                            <Accordion.Header>Para registar uma parcela  necessrio preencher todos os campos? </Accordion.Header>
                            <Accordion.Body>
                                Sim, todos os campos apresentados devem ser preenchidos.
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="1">
                            <Accordion.Header>Existe uma parcela sobreposta  minha. O que posso fazer?</Accordion.Header>
                            <Accordion.Body>
                                Na aba "Minhas Parcelas" pode visualizar todas as suas parcelas atuais. Clicando no boto "Parcelas Prximas"
                                para visualizar parcelas adjacentes s suas. Pode clicar nelas para obter mais informaes ou denunciar a parcela.
                                Clicar no boto de "Reportar" ir encaminh-lo para uma pgina nova onde poder registar a sua queixa. Esta ser
                                posteriormente avaliada por um tcnico ou administrador que ir avaliar a pertinncia da denncia.
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="2">
                            <Accordion.Header>A minha parcela est em estado pendente ou rejeitado. O que aconteceu?</Accordion.Header>
                            <Accordion.Body>
                                Se a sua parcela estiver em estado pendente, no se preocupe, ela ir ser avaliada por um tcnico ou
                                administrador em breve, que aps verificar que tudo est correto ir aprovar a sua parcela. Saber que
                                ela foi aprovada quando o estado dela aparecer como "Aprovada". Se a sua parcela estiver em estado reprovado,
                                algo correu mal e o nosso tcnico ou administrador rejeitou o seu registo. Possivelmente alguma informao ou
                                coordenada foi registada erradamente. Tente editar as informaes de forma a que fiquem corretas. Depois,
                                 s aguardar.
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="3">
                            <Accordion.Header>Enganei-me a registar as coordenadas da minha parcela. O que posso fazer?</Accordion.Header>
                            <Accordion.Body>
                                V  aba "Minhas Parcelas" e na parcela em causa, clique no boto "Editar". Na parte superior da pgina
                                que apareceu existe um mapa onde  possvel clicar para marcar as novas coordenadas. Ateno: todos os pontos
                                da parcela devem ser remarcados, mesmo aqueles que j esto,  partida, corretos, ou seja, toda a parcela deve
                                ser redesenhada. Clique no boto "Confirmar Alteraes" no fundo da pgina.
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="4">
                            <Accordion.Header>Como posso pedir para que algum gerencie a minha parcela? E sob que condies?</Accordion.Header>
                            <Accordion.Body>
                                 Cada pedido de gerenciamento  exclusivo para a parcela requisitada, logo se quiser que todas as suas parcelas
                                 sejam geridas por uma entidade, deve efetuar esse pedido para todas. Para isso, dirija-se  aba "Minhas Parcelas"
                                 e clique no boto "Editar" da parcela em causa. Se esta parcela ainda no estiver atribuda a nenhuma entidade
                                 (de notar que s  possvel atribuir um gerente por parcela) as entidades disponveis na sua localizao sero
                                 listadas. Nesse caso, desde que existam entidades disponveis,  possvel escolher uma da lista. Isso ir enviar
                                 um pedido  entidade. Para concluir esta operao, dever aguardar que a entidade a que enviou o seu pedido o aceite.
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="5">
                            <Accordion.Header> possvel anular o gerenciamento de uma parcela por uma entidade?</Accordion.Header>
                            <Accordion.Body>
                                Sim. Para isso, dirija-se  aba "Minhas Parcelas" e clique no boto "Editar" da parcela em causa. Se esta parcela
                                estiver atribuda a uma entidade, ela aparecer no campo devido. Para remover a entidade, basta clicar em
                                "Remover gerente".
                            </Accordion.Body>
                        </Accordion.Item>
                        <Accordion.Item eventKey="6">
                            <Accordion.Header>Como funcionam as notas?</Accordion.Header>
                            <Accordion.Body>
                                As notas referem-se aos pontos de confiana de um utilizador. Um utilizador que faa contribuies positivas (denncias
                                consideradas pertinentes pelos tcnicos) ganhar pontos de confiana, o que influenciar a subida da nota. Caso o
                                utilizador faa contribuies que atrapalhem o bom funcionamento do site, a sua nota ir descer. Existem 5 notas possveis:
                                de 1 a 5, sendo 1 a pior e 5 a melhor. A nota inicial  2. Os utilizadores com notas superiores tero prioridade sobre as
                                denncias feitas por utilizadores com notas mais baixas.  possvel verificar a sua nota no perfil de utilizador.
                            </Accordion.Body>
                        </Accordion.Item>
                    </Accordion>
                </div>
            </div>

        </>
    )
}

export default FAQ