import  './LoggedHomepage.css'
import TopBar from '../TopBar/TopBar.js'
import CheckIfLoggedOut from "../util/CheckIfLoggedOut";
import {Button, Carousel, Modal} from "react-bootstrap";
import {GoogleMap, LoadScript, Polygon} from "@react-google-maps/api";
import React, { Component }  from 'react';
import CSVConverter from "../util/CSVConverter";
import {Link, useNavigate} from "react-router-dom";
import CheckIfActive from "../util/CheckIfActive";

const LoggedHomepage = () => {
    const navigate = useNavigate();
    let role = localStorage.getItem('role');
    console.log(role);

    function registerButton(){
        if(role === 'D'){
            navigate("/map");
        }
        else if(role.includes("A") || role.includes("B")){
            navigate("/map-admin");
        }
    }

    function parcelsButton(){
        switch (role) {
            case 'D':
                navigate("/my-parcels");
                break;
            case 'C':
                navigate("/parcels-entity");
                break;
            case 'B':
                navigate("/all-parcels");
                break;
            case 'A':
                navigate("/all-parcels-admin");
                break;
        }
    }

    function statisticsButton(){
        if(role === 'D'){
            navigate("/statistics");
        }
        else if(role === "C"){
            navigate("/statistics-entity");
        }
        else if(role.includes("B")){
            navigate("/statistics-technician");
        }
        else if(role.includes("A")){
            navigate("/statistics-admin");
        }
    }

    function faqButton(){
        navigate("/faq");
    }

    return(
        <>
            <CheckIfLoggedOut />
            <CheckIfActive />
            <TopBar />

            <CSVConverter/>

            <div className="lgh_body">

                <div className="hero-register_LoggedHome">
                    <div className="hero-register-text_LoggedHome">
                        <h1><b>Registe as suas parcelas</b></h1>
                        No nosso site  possvel registar as suas parcelas de forma rpida e fcil. <br/>
                        Clique na aba "Registar Parcelas". Clique no mapa para desenhar o seu terreno e marcar <br/>
                        os seus limites e registe as informaes respetivas ao mesmo do lado direito, preenchendo <br/>
                        e selecionando os campos exigidos. Por fim, clique em Submeter. <br/>
                        <p></p>
                            <Button onClick={() => registerButton()} variant="dark">Registe j</Button>
                    </div>
                </div>

                <div className="hero-myParcels_LoggedHome">
                    <div className="hero-myParcels-text_LoggedHome">
                        <h1><b>Verifique as suas parcelas</b></h1>
                        O site E-Floresta permite-lhe guardar toda a informao sobre os seus terrenos num nico stio,<br/>
                        para um acesso rpido e fcil. Clique na aba "Minhas Parcelas". O mapa do lado esquerdo contm <br/>
                        marcados todos os seus terrenos registados. Do lado direito, existe um carto para cada uma das <br/>
                        propriedades registadas. Clique em "Detalhes" para ver todas as informaes sobre uma parcela. <br/>
                        Clique em "Editar" para alterar alguma informao sobre a parcela em causa. <br/>
                        <p></p>
                        <Link to="/my-parcels">
                            <Button onClick={() => parcelsButton()} variant="dark">Verifique as suas parcelas</Button>
                        </Link>
                    </div>
                </div>

                <div className="hero-stats_LoggedHome">
                    <div className="hero-stats-text_LoggedHome">
                        <h1><b>Estatsticas personalizadas</b></h1>
                        O E-Floresta possui estatsticas personalizadas sobre as suas parcelas registadas para facilitar <br/>
                        ainda mais a organizao e o gerenciamento dos seus terrenos. Para lhes aceder, clique na aba "Estatsticas". <br/>
                        <p></p>
                        <Link to="/statistics">
                            <Button onClick={() => statisticsButton()} variant="dark">Veja as estatsticas</Button>
                        </Link>
                    </div>
                </div>

                <div className="hero-faq_LoggedHome">
                    <div className="hero-faq-text_LoggedHome">
                        <h1><b>Dvidas sobre o site?</b></h1>
                        O nosso site possui uma rea onde reunimos algumas das dvidas mais comuns relativamente <br/>
                        ao funcionamento do mesmo. Talvez alguma das suas perguntas esteja respondido nessa rea. <br/>
                        Para lhe aceder, clique na aba "FAQ". <br/>
                        <p></p>
                        <Link to="/faq">
                            <Button onClick={() => faqButton()} variant="dark">Perguntas frequentes</Button>
                        </Link>
                    </div>
                </div>

            </div>
        </>
    )
}

export default LoggedHomepage
